import React from 'react'
import UnLoggedHeader from '../unit/UnLoggedHeader'
import Footer from '../unit/Footer'
import HomeArticle from './HomeArticle'

class UnLoggedHome extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <div>
                <UnLoggedHeader/>
                <HomeArticle logged={false}/>
                <Footer/>
            </div>
        )
    }
}

export default UnLoggedHome;