import React from 'react'
import LoggedHeader from '../unit/LoggedHeader'
import './vote.css'
import { Link, NavLink } from 'react-router-dom';
import ItemType from '../item/ItemType'
import {CheckedCorMBox, UnCheckedCorMBox} from '../unit/CorMBox'
import {OpenItem, CloseItem} from '../item/Item'

class VoteManage extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {

        return (
            <div className="vote">
                <LoggedHeader />
                <div className="vote-article">
                    <div className="vote-main">
                        <div className="vote-left">
                            
                            
                            <Link to="voteCreate" className="none-text-decoration">
                                <UnCheckedCorMBox name="创建投票"></UnCheckedCorMBox>
                            </Link>
                            <div className="vote-left-aline"></div>
                            <CheckedCorMBox name="管理投票"></CheckedCorMBox>
                        </div>
                        <div className="vote-middle">

                        </div>
                        <div className="vote-right">
                            <ItemType name="已开放投票"></ItemType>
                            <div className="vote-right-bottom">
                                <OpenItem name="你想返校么" time="2020-4-13 8:30 至 2020-4-15 23:00"></OpenItem>
                                <OpenItem name="你想返校么" time="2020-4-13 8:30 至 2020-4-15 23:00"></OpenItem>
                                <OpenItem name="你想返校么" time="2020-4-13 8:30 至 2020-4-15 23:00"></OpenItem>
                            </div>
                            <div className="vote-right-aline">
                            </div>
                            <ItemType name="未开放投票"></ItemType>
                            <div className="vote-right-bottom">
                                <CloseItem name="你想返校么" time="2020-4-13 8:30 至 2020-4-15 23:00"></CloseItem>
                                <CloseItem name="你想返校么" time="2020-4-13 8:30 至 2020-4-15 23:00"></CloseItem>
                                <CloseItem name="你想返校么" time="2020-4-13 8:30 至 2020-4-15 23:00"></CloseItem>
                            </div>
                            <div className="vote-right-aline">
                            </div>
                            {/* <img className="vote-right-img" src={require("./vote2.png")}></img> */}
                        </div>
                        <Link to="teacherHome">
                            <img className="vote-back" src={require("../assert/back.png")}></img>
                        </Link>
                    </div>
                </div>
            </div>
        );
    }
}

export default VoteManage;