import React from 'react'
import LoggedHeader from '../unit/LoggedHeader'
import './exam.css'
import { Link, NavLink } from 'react-router-dom';
import ItemType from '../item/ItemType'
import {CheckedCorMBox, UnCheckedCorMBox} from '../unit/CorMBox'
import {OpenItem, CloseItem} from '../item/Item'

class ExamManage extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {

        return (
            <div className="exam">
                <LoggedHeader />
                <div className="exam-article">
                    <div className="exam-main">
                        <div className="exam-left">
                            
                            
                            <Link to="examCreate" className="none-text-decoration">
                                <UnCheckedCorMBox name="创建考试"></UnCheckedCorMBox>
                            </Link>
                            <div className="exam-left-aline"></div>
                            <CheckedCorMBox name="管理考试"></CheckedCorMBox>
                        </div>
                        <div className="exam-middle">

                        </div>
                        <div className="exam-right">
                            <ItemType name="已开放考试"></ItemType>
                            <div className="exam-right-bottom">
                                <OpenItem name="第三百四十九期理论答题" time="2020-4-13 8:30 至 2020-4-15 23:00"></OpenItem>
                                <OpenItem name="第三百四十九期理论答题" time="2020-4-13 8:30 至 2020-4-15 23:00"></OpenItem>
                                <OpenItem name="第三百四十九期理论答题" time="2020-4-13 8:30 至 2020-4-15 23:00"></OpenItem>
                            </div>
                            <div className="exam-right-aline">
                            </div>
                            <ItemType name="未开放考试"></ItemType>
                            <div className="exam-right-bottom">
                                <CloseItem name="第三百四十九期理论答题" time="2020-4-13 8:30 至 2020-4-15 23:00"></CloseItem>
                                <CloseItem name="第三百四十九期理论答题" time="2020-4-13 8:30 至 2020-4-15 23:00"></CloseItem>
                                <CloseItem name="第三百四十九期理论答题" time="2020-4-13 8:30 至 2020-4-15 23:00"></CloseItem>
                            </div>
                            <div className="exam-right-aline">
                            </div>
                            {/* <img className="exam-right-img" src={require("./exam2.png")}></img> */}
                        </div>
                        <Link to="teacherHome">
                            <img className="exam-back" src={require("../assert/back.png")}></img>
                        </Link>
                    </div>
                </div>
            </div>
        );
    }
}

export default ExamManage;