import React from 'react'
import LoggedHeader from '../unit/LoggedHeader'
import './question.css'
import { Link, NavLink } from 'react-router-dom'
import ManageSubject from '../subject/ManageSubject'
import SingleChoice from '../subject/SingleChoice'
import MultiChoice from '../subject/MultiChoice'
import TextSubject from '../subject/TextSubject'

import {CheckedCorMBox, UnCheckedCorMBox} from '../unit/CorMBox'
import ItemCreate from '../item/ItemCreate'

class QuestionCreate extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {

        return (
            <div className="question">
                <LoggedHeader />
                <div className="question-article">
                    <div className="question-main">
                        <div className="question-left">
                            <CheckedCorMBox name="创建问卷"></CheckedCorMBox>
                            <div className="question-left-aline"></div>
                            <Link to="questionManage" className="none-text-decoration">
                                <UnCheckedCorMBox name="管理问卷"></UnCheckedCorMBox>
                            </Link>
                        </div>

                        <div className="question-middle">

                        </div>
                        
                        <div className="question-right">
                            <ItemCreate name="问卷"></ItemCreate>
                            
                            <ManageSubject></ManageSubject>

                            <div className="question-right-bottom">
                                <SingleChoice id={1}></SingleChoice>
                                <SingleChoice id={2}></SingleChoice>
                                <SingleChoice id={3}></SingleChoice>
                                <MultiChoice id={4}></MultiChoice>
                                <TextSubject id={5}></TextSubject>
                            </div>
                        </div>

                        <Link to="teacherHome">
                            <img className="question-back" src={require("../assert/back.png")}></img>
                        </Link>
                    </div>
                </div>
            </div>
        );
    }
}

export default QuestionCreate;