import React from 'react'
import {Link, NavLink} from 'react-router-dom';
import './header.css'

class UnLoggedHeader extends React.Component {
    render() {
        return (
            <div className="logout-header">
                <img className="cc" src={require("./cc.png")}></img>
                <Link to="teacherLogin"><div className="login-teacher">教师登录</div></Link>
                <div className="login-border"></div>
                <Link to="studentLogin"><div className="login-student">学生登录</div></Link>
            </div>
        );
    }
}

export default UnLoggedHeader;