import React from 'react'
import UnLoggedHeader from '../unit/UnLoggedHeader'
import Footer from '../unit/Footer'
import LoginArticle from './LoginArticle'

class TeacherLogin extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <div>
                <UnLoggedHeader/>
                <LoginArticle/>
                <Footer/>
            </div>
        )
    }
}

export default TeacherLogin;